<template>
  <div class="container">
    <!-- <el-row v-for="item in AData" :key="item.id"> -->
    <el-row v-for="item in showData" :key="item.id">
      <el-col :span="24">
        <el-container>
          <el-aside>
            <img :src="item.src" alt />
          </el-aside>
          <el-container>
            <el-header>{{item.name}}</el-header>
            <el-main>{{item.description}}</el-main>
            <el-footer>
              <el-rate disabled show-score text-color="#ff9900" :value="item.score"></el-rate>
            </el-footer>
          </el-container>
        </el-container>
      </el-col>
    </el-row>
    <el-pagination
      :page-sizes="[15, 10, 5]"
      :page-size="pageSize"
      :pager-count="5"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      :current-page="currentPage"
      @current-change="currentPageChange"
      @size-change="sizeChange"
    ></el-pagination>
  </div>
</template>

<script>
import data from '../data.js'
export default {
  data: function() {
    return {
      recommendData: data,
      pageSize: 10,
      currentPage: 1
    }
  },
  computed: {
    total: function() {
      return this.recommendData.length
    },
    showData: function() {
      var minIndex = (this.currentPage - 1) * this.pageSize
      var maxIndex = minIndex + this.pageSize
      return this.recommendData.slice(minIndex, maxIndex)
    }
  },
  methods: {
    currentPageChange: function(newPage) {
      this.currentPage = newPage
    },
    sizeChange: function(newSize) {
      this.pageSize = newSize
    }
  }
}
</script>

<style scoped>
.el-header {
  height: auto !important;
  font-weight: bold;
}

.el-row {
  width: 40vw;
  min-width: 300px;
  padding: 30px;
  padding-right: 10px;
  height: auto;
  padding-bottom: 10px;
  box-shadow: 1px 1px 4px #888888;
  border-radius: 25px;
  background-color: #fff;
  margin: 20px auto;
}
img {
  border-radius: 10%;
  width: 100%;
  height: 100%;
}
.el-main {
  padding: 20px;
  padding-bottom: 13px;
}
.el-aside {
  height: 10vw !important;
  width: 10vw !important;
  overflow-y: hidden;
}
.el-container {
  height: auto !important;
  width: auto !important;
}
.el-footer {
  height: auto !important;
  margin-bottom: 10px;
}
.el-rate {
  height: auto !important;
}
.el-pagination {
  width: 40vw !important;
  margin: 0 auto;
  padding-left: 5vw;
}
</style>
